import React, { useEffect, useState } from "react";
import PouchDB from "pouchdb";
import "./Sessions.scss";
import { useNavigate } from "react-router-dom";

const Sessions = () => {
	const [mydb, setMydb] = useState([]);
	const navigate = useNavigate();
	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/sessions");
	useEffect(() => {
		db.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
				console.log(doc.rows);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	const clickAddSession = () => {
		navigate("/add-sessions");
	};

	return (
		<div className='clients-page'>
			<h1>Sessions</h1>
			<div className='client-calendar'>
				<table>
					<tr>
						<td>client_name</td>
						<td>session_start</td>
						<td>session_end</td>
					</tr>
					{mydb.map((doc) => {
						let temp = doc.doc;
						return (
							<tr key={temp._id}>
								<td>{temp.client_name}</td>
								<td>{temp.session_start}</td>
								<td>{temp.session_end}</td>
							</tr>
						);
					})}
				</table>
			</div>
			<button className='add-client-button' onClick={clickAddSession}>
				Add New Session
			</button>
		</div>
	);
};

export default Sessions;
