import React, { useState, useEffect } from "react";
import PouchDB from "pouchdb";
import "./AddPayment.css";

const AddPayment = () => {
	const [mydb, setMydb] = useState([]);
	const [enteredName, setEnteredName] = useState("Select Name");
	const [clientId, setClientID] = useState("");
	const [enteredAmount, setEnteredAmount] = useState("");
	const [enteredNoOfMonths, setEnteredNoOfMonths] = useState("");
	const [enteredFromMonth, setEnteredFromMonth] = useState("");
	const [enteredPaidDate, setEnteredPaidDate] = useState("");
	const [btn, setBtn] = useState(false);

	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/payments");
	let db_client = new PouchDB("http://127.0.0.1:5984/clients");

	const nameChangeHandler = (event) => {
		setEnteredName(event.target.value);
		const selectedIndex = event.target.options.selectedIndex;
		const client_id =
			event.target.options[selectedIndex].getAttribute("client-id");
		setClientID(client_id);
	};
	const amountChangeHandler = (event) => {
		setEnteredAmount(event.target.value);
	};
	const noMonthsChangeHandler = (event) => {
		setEnteredNoOfMonths(event.target.value);
	};
	const fromMonthChangeHandler = (event) => {
		setEnteredFromMonth(event.target.value);
	};

	const addPaymentHandler = (event) => {
		event.preventDefault();
		db.post({
			client_id: clientId,
			client_name: enteredName,
			amount_per_month: enteredAmount,
			no_months: enteredNoOfMonths,
			from_month: enteredFromMonth,
			paid_date: enteredPaidDate,
		})
			.then((res) => {
				console.log(res);
				setBtn(true);
			})
			.catch((err) => {
				console.log(err);
			});
	};
	useEffect(() => {
		let d = new Date();
		setEnteredPaidDate(
			d.getDate() + "/" + (d.getMonth() + 1) + "/" + d.getFullYear()
		);

		db_client
			.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	return (
		<div className='add-payment'>
			<form
				className='add-payment-container'
				onSubmit={addPaymentHandler}
			>
				<h1>Add New Payment</h1>
				<div className='form-input-field'>
					<select
						placeholder='Client Name'
						onChange={nameChangeHandler}
					>
						<option>Select Name</option>
						{mydb.map((doc) => {
							let temp = doc.doc;
							return (
								<option value={temp.name} client-id={temp._id}>
									{temp.name}
								</option>
							);
						})}
					</select>
				</div>
				<div className='form-input-field'>
					<input
						type='number'
						placeholder='Amount per month'
						onChange={amountChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='number'
						placeholder='No of months'
						onChange={noMonthsChangeHandler}
					></input>
				</div>
				<div className='form-input-field input-date'>
					<div>From Month</div>
					<input
						type='month'
						id='start'
						name='start'
						min='2020-1'
						onChange={fromMonthChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<button type='submit' disabled={btn}>
						Add payment
					</button>
				</div>
			</form>
		</div>
	);
};

export default AddPayment;
