import React, { useEffect, useState } from "react";
import PouchDB from "pouchdb";
import "./Payments.scss";
import { useNavigate } from "react-router-dom";

const Payments = () => {
	const [mydb, setMydb] = useState([]);
	const navigate = useNavigate();
	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/payments");
	useEffect(() => {
		db.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
				console.log(doc.rows);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);
	const clickAddPayment = () => {
		navigate("/add-sessions");
	};
	return (
		<div className='clients-page'>
			<h1>Payments</h1>
			<div className='client-calendar'>
				<table>
					<tr>
						<td>client_name</td>
						<td>amount_per_month</td>
						<td>no_months</td>
						<td>from_month</td>
						<td>paid_date</td>
					</tr>
					{mydb.map((doc) => {
						let temp = doc.doc;
						return (
							<tr key={temp._id}>
								<td>{temp.client_name}</td>
								<td>{temp.amount_per_month}</td>
								<td>{temp.no_months}</td>
								<td>{temp.from_month}</td>
								<td>{temp.paid_date}</td>
							</tr>
						);
					})}
				</table>
			</div>
			<button className='add-client-button' onClick={clickAddPayment}>
				Add New Payment
			</button>
		</div>
	);
};

export default Payments;
