import React, { useEffect, useState } from "react";
import PouchDB from "pouchdb";
import "./ClientDetails.scss";
import { useNavigate } from "react-router-dom";

const ClientDetails = () => {
	const [mydb, setMydb] = useState([]);
	const navigate = useNavigate();
	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/clients");
	useEffect(() => {
		db.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
				console.log(doc.rows);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);
	const clickAddClient = () => {
		navigate("/add-client");
	};
	return (
		<div className='clients-page'>
			<h1>Clients</h1>
			<div className='client-calendar'>
				<table>
					<tr>
						<td>user_name</td>
						<td>name</td>
						<td>dob</td>
						<td>gender</td>
						<td>phone_no</td>
						<td>address</td>
					</tr>
					{mydb.map((doc) => {
						let temp = doc.doc;
						return (
							<tr>
								<td>{temp.user_name}</td>
								<td>{temp.name}</td>
								<td>{temp.dob}</td>
								<td>{temp.gender}</td>
								<td>{temp.phone_no}</td>
								<td>{temp.address}</td>
							</tr>
						);
					})}
				</table>
			</div>
			<button className='add-client-button' onClick={clickAddClient}>
				Add New Client
			</button>
		</div>
	);
};

export default ClientDetails;
