import React, { memo, useEffect, useState } from "react";
import PouchDB, { replicate } from "pouchdb";
// TODO: Try This
// PouchDB.plugin(require('pouchdb-find').default);
// &&
// https://github.com/pouchdb/pouchdb/issues/8303
// 		localdb sync with remote(memo)
//		SOLUTION

import "./AdminLogin.scss";
import { Navigate, useNavigate } from "react-router-dom";
const AdminLogin = () => {
	const navigate = useNavigate();
	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/trainer-login");

	const [mydb, setMydb] = useState({});
	const [queryResult, setQueryResult] = useState(false);
	const [enteredEmail, setEnteredEmail] = useState("");
	const [enteredPassword, setEnteredPassword] = useState("");

	useEffect(() => {
		db.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
				// console.log(doc.rows)
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	const validateAdmin = (event) => {
		event.preventDefault();
		if (
			enteredEmail.trim().length <= 0 &&
			enteredPassword.trim().length <= 0
		)
			return;
		db.find({
			selector: {
				user_name: enteredEmail,
				password: enteredPassword,
			},
			limit: 1,
		})
			.then((doc) => {
				let temp = doc.docs;
				setMydb(temp);
				console.log(temp);
				if (!temp.length > 0) {
					setQueryResult(false);
					return;
				}
				setQueryResult(true);
				navigate("/admin-dashboard");
			})
			.catch((err) => {
				console.log(err);
			});
	};

	const passwordChangeHandler = (event) => {
		setEnteredPassword(event.target.value);
	};

	const emailChangeHandler = (event) => {
		setEnteredEmail(event.target.value);
	};

	return (
		<div className='login-form'>
			<form className='login-form-container' onSubmit={validateAdmin}>
				<h1>Admin Login</h1>
				<div className='form-input-field'>
					<input
						type='text'
						placeholder='Email ID'
						onChange={emailChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='password'
						placeholder='Password'
						onChange={passwordChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<button type='submit'>Login</button>
				</div>
			</form>
		</div>
	);
};

export default AdminLogin;
