import React, { useState, useEffect } from "react";
import PouchDB from "pouchdb";
import "./AddSessions.css";

const AddSessions = () => {
	const [mydb, setMydb] = useState([]);
	const [enteredName, setEnteredName] = useState("Select Name");
	const [clientId, setClientID] = useState("");
	const [enteredFrom, setEnteredFrom] = useState();
	const [enteredTo, setEnteredTo] = useState();
	const [btn, setBtn] = useState(false);

	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/sessions");
	let db_client = new PouchDB("http://127.0.0.1:5984/clients");

	const nameChangeHandler = (event) => {
		setEnteredName(event.target.value);
		const selectedIndex = event.target.options.selectedIndex;
		const client_id =
			event.target.options[selectedIndex].getAttribute("client-id");
		setClientID(client_id);
	};
	const toChangeHandler = (event) => {
		const d = new Date(event.target.value);
		setEnteredFrom(
			d.getDate() +
				"/" +
				(d.getMonth() + 1) +
				"/" +
				d.getFullYear() +
				"  " +
				d.getHours() +
				":" +
				d.getMinutes() +
				""
		);
	};
	const fromChangeHandler = (event) => {
		const d = new Date(event.target.value);
		setEnteredTo(
			d.getDate() +
				"/" +
				(d.getMonth() + 1) +
				"/" +
				d.getFullYear() +
				"  " +
				d.getHours() +
				":" +
				d.getMinutes() +
				""
		);
	};

	const addPaymentHandler = (event) => {
		event.preventDefault();
		console.log(enteredName);
		console.log(clientId);
		console.log(enteredFrom);
		console.log(enteredTo);
		db.post({
			client_id: clientId,
			client_name: enteredName,
			session_start: enteredFrom,
			session_end: enteredTo,
		})
			.then((res) => {
				// console.log(res);
				setBtn(true);
			})
			.catch((err) => {
				console.log(err);
			});
	};
	useEffect(() => {
		db_client
			.allDocs({ include_docs: true })
			.then((doc) => {
				setMydb(doc.rows);
			})
			.catch((err) => {
				console.log(err);
			});
	}, []);

	return (
		<div className='add-payment'>
			<form
				className='add-payment-container'
				onSubmit={addPaymentHandler}
			>
				<h1>Add New Session</h1>
				<div className='form-input-field'>
					<select
						placeholder='Client Name'
						onChange={nameChangeHandler}
					>
						<option>Select Name</option>
						{mydb.map((doc) => {
							let temp = doc.doc;
							return (
								<option value={temp.name} client-id={temp._id}>
									{temp.name}
								</option>
							);
						})}
					</select>
				</div>
				<div className='form-input-field input-date'>
					<div>From</div>
					<input
						type='datetime-local'
						onChange={fromChangeHandler}
					></input>
				</div>
				<div className='form-input-field input-date'>
					<div>To</div>
					<input
						type='datetime-local'
						onChange={toChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<button type='submit' disabled={btn}>
						Add payment
					</button>
				</div>
			</form>
		</div>
	);
};

export default AddSessions;
