import React, { useState } from "react";
import PouchDB from "pouchdb";
import "./AddClient.css";

const AddClient = () => {
	const [enteredName, setEnteredName] = useState("");
	const [enteredEmail, setEnteredEmail] = useState("");
	const [enteredGender, setEnteredGender] = useState("");
	const [enteredPassword, setEnteredPassword] = useState("");
	const [enteredPhone, setEnteredPhone] = useState("");
	const [enteredAddress, setEnteredAddress] = useState("");
	const [enteredDate, setEnteredDate] = useState("");
	const [btn, setBtn] = useState(false);

	PouchDB.plugin(require("pouchdb-find").default);
	let db = new PouchDB("http://127.0.0.1:5984/clients");

	const passwordChangeHandler = (event) => {
		setEnteredPassword(event.target.value);
	};

	const genderChangeHandler = (event) => {
		setEnteredGender(event.target.value);
	};

	const addressChangeHandler = (event) => {
		setEnteredAddress(event.target.value);
	};

	const phoneChangeHandler = (event) => {
		setEnteredPhone(event.target.value);
	};

	const dateChangeHandler = (event) => {
		const d = new Date(event.target.value);
		setEnteredDate(
			d.getDate() + "/" + (d.getMonth() + 1) + "/" + d.getFullYear()
		);
	};

	const emailChangeHandler = (event) => {
		setEnteredEmail(event.target.value);
	};

	const nameChangeHandler = (event) => {
		setEnteredName(event.target.value);
	};

	const registerClient = (event) => {
		event.preventDefault();
		db.post({
			name: enteredName,
			email: enteredEmail,
			password: enteredPassword,
			dob: enteredDate,
			gender: enteredGender,
			phone_no: enteredPhone,
			address: enteredAddress,
		})
			.then((res) => {
				console.log(res);
				setBtn(true);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	return (
		<div className='add-client'>
			<form className='add-client-container' onSubmit={registerClient}>
				<h1>Add New Client</h1>
				<div className='form-input-field'>
					<input
						type='text'
						placeholder='Name'
						onChange={nameChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='text'
						placeholder='Email ID'
						onChange={emailChangeHandler}
					></input>
				</div>
				<div className='form-input-field input-date'>
					<div>D.O.B.</div>
					<input
						type='date'
						placeholder='Date of Birth'
						onChange={dateChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='text'
						placeholder='Gender'
						onChange={genderChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='number'
						placeholder='Phone Number'
						onChange={phoneChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='text'
						placeholder='Address'
						onChange={addressChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<input
						type='password'
						placeholder='Temporary Password'
						onChange={passwordChangeHandler}
					></input>
				</div>
				<div className='form-input-field'>
					<button type='submit' disabled={btn}>
						Register
					</button>
				</div>
			</form>
		</div>
	);
};

export default AddClient;
